#signUpPageContent{
    display: flex;
    width: 100%;
    height: 100%;
    min-width: 320px;
    padding: 30px;
    overflow: hidden;
}
#signUpPageLeftSide{
    width: 50%;
}
#signUpPageRightSide{
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('logo.png');
    background-position: center;
    background-size: contain;
    width: 50%;
    background-color: var(--purple);
}
#signUpPageRightSide h1{
    margin-top: 140px;
   color: rgb(209, 219, 228);
   font-family: var(--titleFontFamily)
}
@media screen and (max-width:900px){
    #signUpPageRightSide{ 
        width: 0%;
        visibility: hidden;
    }
    #signUpPageLeftSide{
        width: 100%;
    }
    #signUpPageContent{
        padding: 10px;
    }
}